.nav-bar{
    position:fixed;
    top:0;
    left:0;
    right:0;
    background-color:rgba(0, 0, 0, 0.1);
    color:white;
    font-family: 'Roboto', sans-serif;
    font-size:1.1rem;
    mix-blend-mode: difference;
    z-index:10000;
}

.nav-bar-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width:1440px;
    height:60px;
    padding: 40px var(--indent);
    margin:auto;
}

.nav-bar button{
    text-decoration:none;
    color:currentColor;
    font-size:inherit;
    font-family: inherit;
    background-color:transparent;
    border:none;
    cursor: pointer;
}

.nav-bar button:focus{
    /* outline: 2px solid orange; */
}

.logo{
    display: inline-block;
    width:-webkit-max-content;
    width:max-content;
    flex-shrink: 0;
    cursor: pointer;

}

.nav-links{
    display: flex;
    align-items: center;
}

.nav-links > * + *{
    display:inline-block;
    width:-webkit-max-content;
    width:max-content;
    margin-left:2rem;
}

.borger{
    display: none;
}

.bun{
    width:50px;
    height:2px;
    background-color:white;
    margin:5px;
    border-radius: 1rem;
    -webkit-transform:translate(0) rotate(0deg);
            transform:translate(0) rotate(0deg);
    transition:-webkit-transform 500ms ease;
    transition:transform 500ms ease;
    transition:transform 500ms ease, -webkit-transform 500ms ease;
    pointer-events: none;
}

@media screen and (max-width:700px){
    @-webkit-keyframes enter_nav_links{
        from{-webkit-transform:translate(100vw);transform:translate(100vw)}
        to{-webkit-transform:translate(0%);transform:translate(0%)}
    }
    @keyframes enter_nav_links{
        from{-webkit-transform:translate(100vw);transform:translate(100vw)}
        to{-webkit-transform:translate(0%);transform:translate(0%)}
    }
    @-webkit-keyframes drop_down_nav{
        from{-webkit-transform:translate(0,-100vh);transform:translate(0,-100vh)}
        to{-webkit-transform:translate(0,0);transform:translate(0,0)}
    }
    @keyframes drop_down_nav{
        from{-webkit-transform:translate(0,-100vh);transform:translate(0,-100vh)}
        to{-webkit-transform:translate(0,0);transform:translate(0,0)}
    }

    .nav-bar{
        font-size:1.5rem;
        mix-blend-mode: normal;
    }
    .borger{
        display: flex;
        flex-direction: column;
        padding: 0.5rem 0;
    }

    .borger.active .bun:first-child{
        -webkit-transform:translate(0,6px) rotate(45deg);
                transform:translate(0,6px) rotate(45deg);
    }
    .borger.active .bun:last-child{
        -webkit-transform:translate(0,-6px) rotate(-45deg);
                transform:translate(0,-6px) rotate(-45deg);
    }

    .nav-links > * + *{
        margin-left:unset;
    }
    .nav-links{
        position: absolute;
        width:100%;
        height:calc(100vh - 60px);
        left:0;
        top:60px;
        flex-direction:column;
        align-items:center;
        justify-content: space-evenly;
        background:#12121200;
        -webkit-transform:translate(0,-100vh);
                transform:translate(0,-100vh);
    }
    .nav-links button{
        display: none;
    }
    .nav-links.active button{
        display: inline;
        display: initial;
    }
    .nav-links.active{
        -webkit-animation: drop_down_nav 200ms ease forwards;
                animation: drop_down_nav 200ms ease forwards;
        transition: background-color 200ms ease;
        background:#121212;
        
        z-index: -1;
    }

    .nav-links.active > *{
        --ms-ani-delay:200ms;
        -webkit-transform:translate(100vw);
                transform:translate(100vw);
        animation: enter_nav_links 300ms var(--ms-ani-delay) ease forwards;
    }
    .nav-links.active > *:nth-child(2){
        animation-delay: calc(var(--ms-ani-delay) + 100ms)
    }
    .nav-links.active > *:nth-child(3){
        animation-delay: calc(var(--ms-ani-delay) + 150ms)
    }
    .nav-links.active > *:nth-child(4){
        animation-delay: calc(var(--ms-ani-delay) + 250ms)
    }
    .nav-links.active > *:nth-child(5){
        animation-delay: calc(var(--ms-ani-delay) + 300ms);
    }
}
.toggle-container{
    --toggle-size:1.5rem;
    width:3rem;
    border:none;
    border-radius:1rem;
    background:rgb(70, 70, 70) !important;
    flex-shrink:0;
    overflow:hidden;
    -webkit-transform:translateY(0);
            transform:translateY(0); /* safari overflow:hidden breaks without this */
}



.toggle-wrapper{
    width:100%;
    height:100%;
    background:white;
    border-radius:inherit;
    transition:-webkit-transform 0.2s ease-in-out;
    transition:transform 0.2s ease-in-out;
    transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}



.toggle-container.active .toggle-wrapper{
    -webkit-transform: translate(calc(-100% + var(--toggle-size)));
            transform: translate(calc(-100% + var(--toggle-size)));
}


.toggle-butt{
    
    width:var(--toggle-size);
    height:var(--toggle-size);
    background-color: white;
    border-radius: 50%;
    margin-left:auto;
    box-shadow:0 0 2px 0 black;

}
